@mixin flex($direction, $justify, $align) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}

@mixin header() {
  font-family: "Staatliches", sans-serif;
  font-weight: 300;
}

@mixin defaultFont() {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}
