@import "../../styling/variables";
@import "../../styling/mixins";

.teamSection {
  padding: 2.5vh 0;
  background-color: $black;
  text-align: center;
  h3 {
    font-size: 1.5rem;
    color: $white;
    margin-bottom: 2.5vh;
  }
  div {
    @include flex(column, center, center);
  }
}

.curved {
  position: relative;
  background: $white;
  height: 5vh;
}

.curved::after {
  content: "";
  border-top-left-radius: 50% 100%;
  border-top-right-radius: 50% 100%;
  position: absolute;
  bottom: 0;
  width: 100%;
  background: $black;
  height: 2.5vh;
}

@media screen and (min-width: 768px) {
  .teamSection {
    padding: 2.5vh 0;
    background-color: $black;
    text-align: center;
    h3 {
      font-size: 2.25rem;
    }
    div {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
}
